








































import Vue from "vue";
import store from "@/store";

export default Vue.extend({
  props: {
    data: {
      type: Object,
      required: false
    }
  },
  methods: {
    openDialog(title: string, category: string) {
      store.commit("application/updateDialog", {
        title: title,
        category: category,
        show: true
      });
    }
  }
});
