
























import Vue from "vue";
export default Vue.extend({
  props: {
    data: {
      type: Object,
      required: false,
    },
    height: {
      type: Number,
      required: false,
    },
    width: {
      type: Number,
      required: false,
    },
    align: {
      type: String,
      required: false,
      default: "align-center",
    },
  },
  computed: {
    name: {
      get() {
        let result = "";
        const propertyExist = this.data.toString().indexOf("categories") !== -1;
        if (!propertyExist) {
          for (const item in this.data.categories) {
            result = this.data.categories[item].name;
          }
        }
        return result;
      },
    },
  },
});
