 * TEMPLATE
 * @name Dialog
 * @description Component for dialog
 * @author Laurent Grimaldi
 */
import Vue from "vue";
import { mapState } from "vuex";

export default Vue.extend({
  name: "Dialog",
  props: {
    title: {
      type: String,
      required: false,
    },
    mode: {
      type: String,
      required: false,
      default: "modal",
    },
    action: {
      type: Boolean,
      required: false,
      default: false,
    },
    show: {
      type: Boolean,
      required: false,
      default: false,
    },
    size: {
      type: String,
      required: false,
      default: "sd",
    },
    content: {
      type: Object,
      required: false,
    },

    disabled: { type: Boolean, required: false, default: false },

    noclick: { type: Boolean, required: false, default: false },
  },
  data() {
    return {
      sizeDialog: "",
      picture: {
        id: 0,
        source: "",
        title: "",
      },
    };
  },
  mounted() {
    switch (this.size) {
      case "sd":
        this.sizeDialog = "300";
        break;
      case "md":
        this.sizeDialog = "500";
        break;
      case "ld":
        this.sizeDialog = "90%";
        break;
    }
  },
  computed: {
    showDialog: {
      get(): boolean {
        if (this.content.hasOwnProperty("data")) {
          this.$set(this.$data, "picture", this.content.data.logo);
        }

        return this.show;
      },
      set(value: boolean) {
        this.$emit("update:show", value);
      },
    },
  },
  methods: {
    closeBtn() {
      this.showDialog = false;
    },
  },
});
